"use client"

import { useState, useEffect } from "react"
import * as tf from "@tensorflow/tfjs"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Thermometer, Droplets, Sun, Wind, Activity, BrainCircuit, AlertTriangle } from "lucide-react"
import { useToast } from "@/hooks/use-toast"

// Import your rule-based logic
import {
  calculateOccupancy,
  detectAnomalies,
  type SensorData,
  type OccupancyResult,
  type Anomaly,
} from "@/lib/occupancy"

interface Room {
  id: string
  name: string
}

// Scaler values from your Colab
const SCALER_MEAN = [21.4412, 25.3892, 195.7686, 721.1553, 0.0040346]
const SCALER_SCALE = [1.0295, 2.4504, 251.8427, 294.2923, 0.0006132]

function scaleInput(input: number[]): number[] {
  return input.map((val, i) => (val - SCALER_MEAN[i]) / SCALER_SCALE[i])
}

export function RoomAnalyzerForm({ rooms }: { rooms: Room[] }) {
  const { toast } = useToast()

  // ML State
  const [model, setModel] = useState<tf.LayersModel | null>(null)
  const [isModelLoading, setIsModelLoading] = useState(true)
  const [mlError, setMlError] = useState<string | null>(null)

  // Form inputs (shared between ML and rule-based)
  const [selectedRoom, setSelectedRoom] = useState<string>("")
  const [temperature, setTemperature] = useState<string>("23.7")
  const [humidity, setHumidity] = useState<string>("26.2")
  const [light, setLight] = useState<string>("500")
  const [co2, setCo2] = useState<string>("750")
  const [humidityRatio, setHumidityRatio] = useState<string>("0.0047")

  // Results
  const [mlPrediction, setMlPrediction] = useState<number | null>(null)
  const [ruleResult, setRuleResult] = useState<OccupancyResult | null>(null)
  const [anomalies, setAnomalies] = useState<Anomaly[]>([])

  // Load ML model once
  useEffect(() => {
    async function loadModel() {
      try {
        const loadedModel = await tf.loadLayersModel('/model/model.json')
        setModel(loadedModel)
        setIsModelLoading(false)
        toast({ title: "AI Model Ready", description: "Using neural network for prediction" })
      } catch (err: any) {
        console.warn("ML model failed to load:", err.message)
        setMlError(err.message || "Unknown error")
        setIsModelLoading(false)
        toast({
          title: "AI Model Unavailable",
          description: "Falling back to rule-based system",
          variant: "default",
        })
      }
    }
    loadModel()
  }, [toast])

  const handlePredict = async () => {
  if (!selectedRoom) {
    toast({ title: "Room Required", description: "Please select a room.", variant: "destructive" })
    return
  }

  const temp = parseFloat(temperature) || 22
  const hum = parseFloat(humidity) || 25
  const lightVal = parseFloat(light) || 0
  const co2Val = parseFloat(co2) || 400
  const humRatio = parseFloat(humidityRatio) || 0.004

  // Smart mapping from your 5 sensors → realistic rule-based inputs
  const wifiDevices = lightVal > 400 ? 20 : lightVal > 100 ? 8 : 1
  const motionActivity = co2Val > 900 || lightVal > 600 ? "just-now" : co2Val > 600 ? "5-mins" : "no-motion"
  const roomSchedule = co2Val > 1000 ? "class-now" : co2Val > 700 ? "class-empty" : "no-class"
  const electricityUsage = lightVal > 500 ? 50 : lightVal > 200 ? 30 : 10

  const sensorData: SensorData = {
    wifiDevices,
    motionActivity,
    roomSchedule,
    temperature: temp,
    electricityUsage,
  }

  const ruleResult = calculateOccupancy(sensorData)
  const ruleAnomalies = detectAnomalies(sensorData, ruleResult)
  setRuleResult(ruleResult)
  setAnomalies(ruleAnomalies)

  // Try ML first (same as before)
  if (model && !mlError) {
    try {
      const scaled = scaleInput([temp, hum, lightVal, co2Val, humRatio])
      const tensor = tf.tensor2d([scaled])
      const result = (await (model.predict(tensor) as tf.Tensor).data())[0]
      tensor.dispose()

      setMlPrediction(result)
      const isOccupied = result > 0.5
      toast({
        title: `AI: ${isOccupied ? "Occupied" : "Vacant"}`,
        description: `Confidence ${(result * 100).toFixed(1)}% (AI)`,
      })
      return
    } catch (err) {
      console.warn("ML inference failed → using rule-based")
      setMlError("inference")
    }
  }

  // Fallback → show real rule-based result
  const isOccupied = ruleResult.isOccupied
  toast({
    title: `${isOccupied ? "Occupied" : "Vacant"} (Rule-Based)`,
    description: `${ruleResult.confidence}% confidence • ${ruleAnomalies.length} ${ruleAnomalies.length === 1 ? "anomaly" : "anomalies"}`,
    variant: isOccupied ? "default" : "secondary",
  })
}

  const isUsingML = model && !mlError && mlPrediction !== null
  const finalIsOccupied = isUsingML 
    ? (mlPrediction ?? 0) > 0.5 
    : ruleResult?.isOccupied ?? false
  const finalConfidence = isUsingML 
    ? ((mlPrediction ?? 0) * 100).toFixed(1)
    : `${ruleResult?.confidence ?? 0}%`

  return (
    <div className="grid gap-6 lg:grid-cols-2">
      {/* LEFT: Input Form */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2 text-xl">
            <BrainCircuit className="h-5 w-5" />
            AI + Rule-Based Occupancy Detector
          </CardTitle>
          <CardDescription>
            {isModelLoading 
              ? "Loading AI model..." 
              : mlError 
                ? <span className="text-orange-600 flex items-center gap-2">
                    <AlertTriangle className="h-4 w-4" />
                    AI unavailable — using rule-based fallback
                  </span>
                : "AI model ready"}
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-5">
          {/* Same inputs as before */}
          {/* ... (your existing input fields) ... */}
          {/* I'll keep them short for brevity */}
          <div className="space-y-2">
            <Label>Select Room</Label>
            <Select value={selectedRoom} onValueChange={setSelectedRoom}>
              <SelectTrigger><SelectValue placeholder="Choose a room" /></SelectTrigger>
              <SelectContent>
                {rooms.map(r => <SelectItem key={r.id} value={r.id}>{r.name}</SelectItem>)}
              </SelectContent>
            </Select>
          </div>

          <div className="grid gap-4 sm:grid-cols-2">
            <div className="space-y-2">
              <Label><Thermometer className="inline h-4 w-4" /> Temp (°C)</Label>
              <Input type="number" value={temperature} onChange={e => setTemperature(e.target.value)} />
            </div>
            <div className="space-y-2">
              <Label><Droplets className="inline h-4 w-4" /> Humidity (%)</Label>
              <Input type="number" value={humidity} onChange={e => setHumidity(e.target.value)} />
            </div>
            <div className="space-y-2">
              <Label><Sun className="inline h-4 w-4" /> Light (Lux)</Label>
              <Input type="number" value={light} onChange={e => setLight(e.target.value)} />
            </div>
            <div className="space-y-2">
              <Label><Wind className="inline h-4 w-4" /> CO2 (ppm)</Label>
              <Input type="number" value={co2} onChange={e => setCo2(e.target.value)} />
            </div>
            <div className="col-span-2 space-y-2">
              <Label><Activity className="inline h-4 w-4" /> Humidity Ratio</Label>
              <Input type="number" step="0.0001" value={humidityRatio} onChange={e => setHumidityRatio(e.target.value)} />
            </div>
          </div>

          <Button onClick={handlePredict} className="w-full" size="lg">
            Predict Occupancy
          </Button>
        </CardContent>
      </Card>

      {/* RIGHT: Result */}
      <div className="space-y-6">
        {(mlPrediction !== null || ruleResult) ? (
          <Card className={finalIsOccupied ? "border-red-400" : "border-green-400"}>
            <CardContent className="py-12 text-center">
              <h2 className="text-5xl font-bold mb-4">
                {finalIsOccupied ? "Occupied" : "Vacant"}
              </h2>
              <p className="text-2xl text-muted-foreground mb-4">
                Confidence: {finalConfidence}%
              </p>
              {isUsingML ? (
                <p className="text-sm text-green-600">Powered by AI Model</p>
              ) : (
                <p className="text-sm text-orange-600 flex items-center justify-center gap-1">
                  <AlertTriangle className="h-4 w-4" />
                  Powered by Rule-Based Fallback
                </p>
              )}
              {anomalies.length > 0 && (
                <p className="mt-4 text-sm text-red-600">
                  {anomalies.length} {anomalies.length === 1 ? "anomaly" : "anomalies"} detected
                </p>
              )}
            </CardContent>
          </Card>
        ) : (
          <Card className="border-dashed">
            <CardContent className="py-20 text-center text-muted-foreground">
              Click predict to see results
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  )
}